.hawaiian-airlines {
	&--special,
	&--ex-special {
		padding: 8rem 1rem 3rem 1rem;

		.disclaimer {
			margin-top: 40px;
			font-size: smaller;
		}
	}

	&--special {
		h2 {
			margin-top: 40px;
			color: #009686;
			text-transform: uppercase;
			font-weight: bold;
			font-size: 28px;
		}
	}

	&--ex-special {
		.col-xs-12 {
			margin-top: 20px;
		}

		.title-description {
			padding-top: 0;
		}

		.image-description-parent {
			.image-container {
				display: block;

				&:before {
					display: none;
				}

				img {
					width: 100%;
				}
			}

			.text-content {
				border: 1px solid #e6e6e6;
				border-top: 0;
				padding: 20px 10px 40px;
			}
			h4 {
				font-family: "Baskerville BT WGL4 W05 Roman",serif;
				font-size: 1.3rem;
				text-align: center;
			}
		}

		.tour-price {
			float: right;
		}

		.image-description-button {
			transform: translateY(-50%);
			margin: 0 10px;
		}

		.image-description-button a.marriott-btn {
			width: 100%;
			white-space: normal;
		}

		.ha-footer {
			margin-top: 80px;

			h5 {
				color: #009686;
				text-transform: uppercase;
				font-weight: bold;
				font-size: 28px;
			}
		}

		.disclaimer {
			margin-top: 80px;
		}
	}

	a {
		color: #645f5a;
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}

		&.marriott-btn {
			display: block;
			width: 60%;
			margin: 0 auto;
			color: #fff;
			text-decoration: none;
			text-align: center;
		}

	}

	.ha-header-meta {
		display: none;
	}

	.hero-element-info-header {
		padding-bottom: 0;
		margin-bottom: 20px;
	}

	.hero-element-info-body {
		padding-top: 0;

		p + p,
		p + .form-group {
			margin-top: 20px;
		}
	}

	.hero-element .cover-picture-container img {
		width: 100%;
	}


}

.header-info,
.owner-login {
	display: none;
}

.fixed-header {
	position: absolute !important;
}

.navbar:after {
	display: none;
}

.navbar-nav {
	visibility: hidden;
}



.ha-header-meta {
	position: absolute;
	right: 0;
	top: 50%;
	transform: translateY(-50%);

	a {
		color: #159486;
		font-weight: bold;
		text-decoration: none;
	}

	.marriott-btn {
		margin-left: 20px;
		color: #fff;

		&:hover {
			text-decoration: none;
		}
	}
}

.ha-logo {
	display: block;
	margin: 0 auto;
}

.ha-next-step {
	display: block;
	margin: 20px auto 0;
	width: 90%;
	font-size: large;
}

.ha-step--2 {
	display: none;
}

.ha-submit {
	display: block;
	width: 90%;
	margin: 0 auto;
	color: #fff;
	text-decoration: none;
	font-size: large;
}
